.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.hex-value {
  position: relative;
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  font-size: 3vw;
  padding: 2rem;
  cursor: pointer;
}

.hex-copy {
  background: none;
  border: none;
  text-transform: uppercase;
}

.navbar-brand {
  font-size: 1.5em;
}

#container button.random-btn {
  font-size: 1.5em;
  position: absolute;
  bottom: 1rem;
  width: 90%;
  margin: 0 5%;
}

.copied {
  opacity: 0;
  transition: ease-in-out .5s opacity;
}

.copied-true {
  opacity: 1;
}

#random {
  font-size: 1.5em;
  color: whitesmoke;
}
